import React from 'react'
import './form.styles.scss'
import FormInput from '../form-input/form-input'


class Form extends React.Component{
    constructor(){
        super()
        this.state ={
            population: '',
            timeToElapse:'',
            reportedCases: '',
            totalHospitalBeds: '',
            periodType: 'days',
            }
    }
    
    handleChange =e=>{
        const {name, value} = e.target
        switch (name) {
            case 'data-population':
                this.setState({
                    population: value
                })
                break
            case 'data-time-to-elapse':
                this.setState({
                    timeToElapse: value
                })
                break
            case 'data-period-type':
                this.setState({
                    periodType: value
                })
                break
            case 'data-total-hospital-beds':
                this.setState({
                    totalHospitalBeds: value
                })
                break
            case 'data-reported-cases':
                this.setState({
                    reportedCases: value
                })
                break;
        
            default:
                break;
        }
        
    }
    handleClick =()=>{
        console.log(this.state)
        this.setState({
            population: '',
            timeToElapse:'',
            reportedCases: '',
            totalHospitalBeds: '',
            periodType: 'days',
        })
    }

    render (){
        const{ periodType, timeToElapse, reportedCases, population, totalHospitalBeds} = this.state
        return (
            <div className='form-container'>
                <form >
                    <h1 className='heading'>My Covid Estimator</h1>
                    <FormInput
                    handleChange={this.handleChange}
                    name={'data-population'}
                    value={population}
                    label='Poulation'
                    aria-label={'data-population'}
                    required
                    type='number'
                    min='0'
                    />
                    <FormInput handleChange={this.handleChange}
                    name={'data-time-to-elapse'}
                    value={timeToElapse}
                    label='Time To Elapse'
                    aria-label={' data-time-to-elapse'}
                    required
                    type='number'
                    min='0'
                    />
                    <FormInput handleChange={this.handleChange}  
                    name={'data-reported-cases'} 
                    value={reportedCases}
                    label='Reported Cases'
                    aria-label={'data-reported-cases'}
                    required
                    type='number'
                    min='0'
                    />
                    <FormInput handleChange={this.handleChange}
                    name={'data-total-hospital-beds'}
                    value={totalHospitalBeds}
                    label='Total Hospital Beds'
                    aria-label={'data-total-hospital-beds'}
                    required
                    type='number'
                    min='0'
                    />
                    <div>
                        <label htmlFor='periodType' className='select-label'>Period Type</label>
                        <select onChange={this.handleChange} value ={periodType} name='data-period-type' className='form-select' aria-label='select data period type'>
                            <option  value='days' aria-label='days'>Days</option>
                            <option value="weeks" aria-label='weeks'>Weeks</option>
                            <option value="months" aria-label='months'>Months</option>
                        </select>
                    </div>
                    
                    
                    
                        <input
                        onClick={this.handleClick}
                        label='Button go Submit'
                        type='button'
                        name='button-go-submit'
                        className='button-go-submit'
                        value='Submit'
                        />
                    

                </form>
            </div>

        )
    }
}

export default Form